  margin: 0px;
  font-family: "Montserrat", sans-serif;
}

.App_Container__10KOh {
  margin-top: 50px;
  height: 100%;
  min-width: 1200px;
  padding-bottom: 10px;
}

.array-bar{
    width: 2px;
    background-color: rgb(57, 86, 218);
    margin: 0 1px;
    display: inline-block;
}
.Toolbar_Toolbar__ApScI{
    height: 56px;
    width: 100%;
