.Toolbar_Logo__3Lk47{
    height: 80%;
}
@media(max-width: 499px){
    .Toolbar_DesktopOnly__LuPaL {
        display: none;
    }
}
.NavigationItems_NavigationItems__1fnFX {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: 100%;
}

@media (min-width: 500px) {
    .NavigationItems_NavigationItems__1fnFX {
        flex-flow: row;
    }
}
.NavigationItem_NavigationItem__2SpXc {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;   
    width: 100%;
}

.NavigationItem_NavigationItem__2SpXc p {
    color: white;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem_NavigationItem__2SpXc p:hover,
.NavigationItem_NavigationItem__2SpXc p:active,
.NavigationItem_NavigationItem__2SpXc p.NavigationItem_active__2v2td {
    color: #cccc;
}

@media (min-width: 500px) {
    .NavigationItem_NavigationItem__2SpXc {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    
    .NavigationItem_NavigationItem__2SpXc p {
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItem_NavigationItem__2SpXc p:hover,
    .NavigationItem_NavigationItem__2SpXc p:active,
    .NavigationItem_NavigationItem__2SpXc p.NavigationItem_active__2v2td {
        background-color: rgb(119, 159, 174);
        border-bottom: 4px solid #cccc;
        cursor: pointer;
        color: white;
    }
}
.SortingVisualizer_ColumnContainer__16BCK {
  margin-top: 80px;
  display: inline-block;
  min-width: 1000px;
}

.Backdrop {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(26, 25, 25, 0.7);
}

.BackdropOpen {
    display: block;
}
.BackdropClosed{
    display: none;
}
